<style scoped>
.info {
  width: 100%;
}
.info > h2 {
  display: flex;
  justify-content: space-between;
}
.info > h2 > span {
  color: black;
}
.infocenter {
  padding: 24px 40px;
}
.infocenter > p {
  padding: 12px;
  display: flex;
}
.infocenter > p > span:nth-child(1) {
  padding: 12px 24px;
  background: #f3f5f7;
  margin-right: 50px;
}
.infocenter > p > span:nth-child(2) {
  flex: 1;
  padding: 12px 24px;
  border-bottom: 1px solid #d9dde1;
}
.infos {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.form {
  width: 300px;
  height: 300px;
  background-color: #d9dde1;
  border-radius: 16px;
}
.form > label {
  width: 300px;
  display: block;
  padding: 15px 30px;
}
.form > label > input {
  margin-left: 20px;
}
</style>
<template>
  <div>
    <div class="info">
      <h2>
        个人信息
        <span>
          点击修改个人信息
        </span>
      </h2>
      <div class="infocenter">
        <p>
          <span>昵称</span><span>{{ datas.name }}</span>
        </p>
        <p>
          <span>邮箱</span><span>{{ datas.Email }}</span>
        </p>
        <p>
          <span>地址</span><span>{{ datas.address }}</span>
        </p>
        <p>
          <span>签名</span><span>{{ datas.sign }}</span>
        </p>
      </div>
    </div>
    <!-- <div class="infos">
      <div class="form">
        <label>昵称<input type="text" :value="datas.name"/></label>
        <label>地址<input type="text" :value="datas.address"/></label>
        <label>签名<input type="text" :value="datas.sign"/></label>
      </div>
    </div> -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      datas: []
    };
  },
  methods: {
    user() {
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/usersinfo",
        params: {
          uid: JSON.parse(window.sessionStorage.getItem("user")).id,
          email: JSON.parse(window.sessionStorage.getItem("user")).email,
          token: JSON.parse(window.sessionStorage.getItem("user")).token,
          name: JSON.parse(window.sessionStorage.getItem("user")).name
        }
      }).then(res => {
        if (res.data.users) {
          this.datas = res.data.users[0];
          this.$router.push({ name: "user" });
        } else {
          this.$store.commit("LOGOUT");
          this.$router.push({ name: "login" });
        }
      });
    }
  },
  mounted() {
    this.user();
  }
};
</script>
